// html
<template>
  <div id='main'>
    <div id='canteen'>
      <div class='canteen-header'>
        <div id='searchBox'>
          <div id='search' :class="{'fixed-search': !tag}">
            <input type='text' placeholder='搜索食堂、菜品名称'>
          </div>
        </div>
      </div>
      <div class='canteen-content'>
        <div class='canteen-bar'>
          <table>
            <tr>
              <td><div @click="enterRestaurant(0)"><p>文山湖</p></div></td>
              <td><div @click="enterRestaurant(1)"><p>南区</p></div></td>
              <td><div @click="enterRestaurant(2)"><p>新西南</p></div></td>
              <td><div><p>清真</p></div></td>
            </tr>
            <tr>
              <td><div><p>老西南</p></div></td>
              <td><div><p>教工</p></div></td>
              <td><div><p>实验</p></div></td>
              <td><div><p>荔山</p></div></td>
            </tr>
          </table>
        </div>
        <div id='main-menu' class='menu'>
          <div id='recommend'>
            <img src='static/assets/recommend.png' alt='recommend'>
            <p>鸡胸肉扒饭</p>
            <div id='rec-intro'>
              <p>鲜嫩的鸡胸肉，配上特制的酱汁，口感饱满，色味俱佳，让人垂涎欲滴，食指大动。</p>
            </div>
            <img src='static/assets/food.jpeg' alt='food'>
          </div>
          <div>

          </div>
          <div>

          </div>
          <div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

// js
<script>
  import $ from 'jquery'
  export default {
    name: 'center',
    data () {
      return {
        colors: [
          '#FF4500',
          '#FF8C00',
          '#FFA500',
          '#009B77',
          '#1E90FF',
          '#00BFFF',
          '#9932CC',
          '#C71585'
        ],
        tag: false
      }
    },
    methods: {
      renderCanteenColor: function () {
        for (var i = 0; i < $('.canteen-bar table div').length; i++) {
          $('.canteen-bar table div').eq(i).css('background-color', this.colors[i])
        }
      },
      wrapHeader: function () {
        var self = this
        $('#canteen').scroll(function () {
          self.$root.y1 = $(this).scrollTop()
          if ($(this).scrollTop() >= 210) {
            self.tag = self.tag ? self.tag : !self.tag
            if (self.tag) {
              $('#searchBox').addClass('fixed-searchBox')
            }
          } else if (self.tag) {
            $('#searchBox').removeClass('fixed-searchBox')
            self.tag = false
          }
        })
      },
      enterRestaurant: function (i) {
        this.$store.commit('enterRestaurant', this.$store.state.restaurantList[i].id)
        this.$router.push({path: '/restaurant'})
      }
    },
    mounted () {
      this.$root.eventHub.$emit('shuffle', 0)
      this.renderCanteenColor()
      $('#canteen').scrollTop(this.$root.y1)
      this.wrapHeader()
    }
  }
</script>

// css
<style scoped>
  #canteen{
    width: 100%;
    height: calc(100% - 140px);
    overflow: scroll;
  }
  .canteen-header{
    width: 100%;
    height: 360px;
    background: url('../../static/assets/header.jpg') 0 56%;
    background-size: cover;
  }
  #searchBox{
    width: 100%;
    height: 150px;
    position: fixed;
    top: 0;
    z-index: 2;
  }
  .fixed-searchBox{
    background-color: #1296db;
  }
  #search{
    width: 80%;
    height: 100px;
    border-radius: 50px;
    background-color: white;
    position: fixed;
    top: 25px;
    left: 10%;
    z-index: 3;
    overflow: hidden;
  }
  .fixed-search{
    box-shadow: 0 3px 8px #707070;
  }
  #search input{
    width: calc(100% - 100px);
    height: 100%;
    font-size: 36px;
    border: 0;
    outline: 0;
    position: relative;
    left: 50px;
  }
  .canteen-bar{
    width: 100%;
    height: 360px;
    margin: 12px 0;
  }
  .canteen-bar a{
    text-decoration: none;
    color: white;
  }
  .canteen-bar table{
    width: 88%;
    height: 100%;
    margin: 0 6%;
  }
  .canteen-bar table div{
    width: 140px;
    height: 140px;
    border-radius: 70px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
  }
  .canteen-bar table div p{
    color: white;
    font-size: 36px;
    font-weight: 500;
    border: 0;
    width: 80%;
    text-align: center;
    position: relative;
    top: 50%;
    left: 10%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
  }
  #main-menu{
    width: 80%;
    margin: 0 10%;
  }
  .menu{
    height: auto;
  }
  .menu>div{
    width: 100%;
    height: 340px;
    overflow: hidden;
  }
  .menu>div:first-of-type{
    border-top: 3px solid #f0f2f5;
  }
  .menu>div:not(:last-of-type){
    border-bottom: 3px solid #f0f2f5;
  }
  #recommend{
    height: 347px;
  }
  #recommend img:first-of-type{
    width: 52px;
    padding: 37px 25px 8px 0;
    float: left;
  }
  #recommend>p:first-of-type{
    font-size: 48px;
    font-weight: bold;
    line-height: 140%;
    padding-top: 30px;
    float: left;
  }
  #rec-intro{
    clear: left;
    width: calc(100% - 240px);
    height: calc(100% - 116px);
    display: table;
    float: left;
  }
  #rec-intro p{
    width: 100%;
    height: 100%;
    font-size: 36px;
    line-height: 200%;
    padding: auto;
    vertical-align: middle;
    display: table-cell;
  }
  #recommend img:last-of-type{
    width: 180px;
    height: 180px;
    border-radius: 12px;
    margin: 120px 0 45px 60px;
  }
</style>



